body {
  background-color: #007bff;
}

#map {
  width: 100%;
  height: 100vh;
}

.leaflet-left {
  left: 95% !important;
  top: 35px !important;
}

.zoneAlert {
  color: white;
  text-align: center;
}

.recherche {
  position: absolute;
  left: 45px;
  top: 45px;
  z-index: 500;
  background-color: white;
  border-radius: 5px;
  display: block;
  width: 500px;
  text-align: center;
}

.info {
  position: absolute;
  left: 45px;
  padding: 7px;
  background: white;
  border-radius: 5px;
  z-index: 500;
}

.info p {
  display: inline-block;
  margin: 0;
}

.searchbar {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 10px 15px 10px 15px;
  border-radius: 10px;
  background-color: white;
}

.searchbar input {
  width: 85%;
  border: none;
}

.searchbar input:focus {
  outline: none;
  border: none;
}

.searchbar a {
  width: 5%;
}

@media (min-width: 992px) {
  .info {
    top: 120px;
  }
}

@media (max-width: 576px) {
  .recherche {
    position: relative;
    width: 80%;
    margin: auto;
    display: block;
    left: 0;
    top: 75px;
    box-shadow: 0 0 3px black;
  }
  .leaflet-control-container {
    display: none;
  }
  #map {
    position: absolute !important;
  }
  .info {
    width: 100%;
    bottom: 0;
    left: 0;
    height: 75px;
    line-height: 65px;
    border-radius: 0;
    text-align: center;
  }
}

.container {
  width: 85vw;
}

.full-width {
  width: 100%;
}

.margin-constraint {
  display: flex;
  flex-direction: column;
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.useful-width {
  width: 100%;
  max-width: 1200px;
}

body {
  font-family: "Product Sans";
}
